*,
*::before,
*::after {
	box-sizing: inherit;
}

html {
	box-sizing: border-box;
	font-family: $font-family-stem;
}

// header 
.header {
	position: fixed;
	top: 0;
	right: 0;
	left: 0;
	z-index: 9;
	box-shadow: 0 0 20px rgba(0, 0, 0, 0.25);
	background-color: $color-white;
	&__container {
		display: flex;
		justify-content: space-between;
		align-items: center;
		height: 100px;
	}

	&__logo {
		margin-right: 30px;
		margin-left: 30px;
	}

	&__info {
		display: flex;
		justify-content: space-between;
		align-items: center;
		margin-right: 90px;
	}

	&__login {
		margin-right: 40px;
	}

	&__name {
		font-size: 24px;
		font-weight: 700;
		color: $color-mine-shaft;
	}

	&__swither {
		font-size: 14px;
		color: $color-curious-blue;
		text-decoration: underline;
	}

	&__notice {
		position: relative;
		margin-right: 5px;
	}

	&__notice-quantity {
		position: absolute;
		top: -9px;
		right: -5px;
		width: 18px;
		height: 18px;
		background: $color-sunset-orange;
		font-size: 12px;
		color: $color-white;
		text-align: center;
		line-height: 18px;
		border-radius: 50%;
	}

}

// aside
.sidebar {
	position: fixed;
	top: 0;
	bottom: 0;
	left: 0;
	z-index: 8;
	width: 250px;
	padding-top: 100px;
	box-shadow: 0 0 4px rgba(0, 0, 0, 0.25);
	background: linear-gradient(to top, #243873, #1b96d1);
	
	&__menu {
		margin-top: 60px;
		margin-bottom: 0;
		padding-left: 0;
		list-style: none;
	}

	&__link {
		display: flex;
		align-items: center;
		height: 80px;
		padding-left: 30px;
		box-sizing: border-box;
		color: $color-periwinkle;
		text-decoration: none;
		cursor: pointer;
		
		.dashboard {
			background: url("../images/dashboard.svg") center no-repeat;			
		
		}
		
		.sponsor {
			background: url("../images/sponsor.svg") center no-repeat;			
		
		}

		.handshake {
			background: url("../images/handshake.svg") center no-repeat;			
		
		}

		.network {
			background: url("../images/network.svg") center no-repeat;			
		
		}

		.dashboard {
			background: url("../images/dashboard.svg") center no-repeat;			
		
		}

		&:hover,
		&.active {
			color: $color-white;
			background: $color-curious-blue;
			
			.dashboard {
				background: url("../images/dashboard_active.svg") center no-repeat;			
			}
			
			.sponsor {
				background: url("../images/sponsor_active.svg") center no-repeat;			
			}
			
			.handshake {
				background: url("../images/handshake_active.svg") center no-repeat;			
			}
			
			.network {
				background: url("../images/network_active.svg") center no-repeat;			
			}
		}
	}
	&__image {
		width: 32px;
		height: 32px;
		margin-right: 15px;
	}
}
.dashboard {
	&__container {
		width: 1920px;
		max-width: 100%;
		padding-top: 100px;
		padding-left: 305px;
		padding-right: 35px;
		box-sizing: border-box;

	}	
	
	&__header {
		width: 100%;
		margin-top: 40px;
		margin-bottom: 40px;
		padding-right: 15px;
		padding-left: 15px;	
	}
	
	&__statistic,
	&__title {
		margin-top: 0;
		margin-bottom: 0;
		color: $color-mine-shaft;
		font-size: 36px;
		font-weight: 700;
	}
	
	&__title {
		display: inline-block;
		margin-right: 30px;
	}

	&__edit {
		display: inline-block;
		color: $color-silver;
		font-size: 14px;
		font-weight: 700;
		text-decoration: none;

		&::before {
			display: inline-block;
			width: 8px;
			height: 8px;
			margin-right: 4px;
			content: '';
			background: url("../images/edit.png") center no-repeat;	
			background-size: contain;	
		}
	}

	&__item {
		min-height: 200px;
		margin-right: 15px;
		margin-bottom: 30px;
		margin-left: 15px;
		box-shadow: 4px 4px 40px rgba(29, 149, 209, 0.1);
		border-radius: 5px;
		background-color: $color-white;

	}
}

p {
	margin-top: 0;
	margin-bottom: 0;
}

.dashboard__row {
	display: flex;
	flex-wrap: wrap;
	&_first {
		margin-bottom: 30px;
	}
	&_second {
		flex-direction: column;
	}
}

.dashboard-brand {
	display: flex;
	align-items: stretch;
	width: 760px;
	&__image {
		display: flex;
		justify-content: center;
		align-items: center;
		min-width: 200px;
		background: $color-alabaster;
	}

	&__content {
		padding: 40px;
	}

	&__header {
		margin-top: 0;
		margin-bottom: 20px;
		color: $color-curious-blue;
		font-size: 36px;
		font-weight: 700;
	}

	&__text {
		margin-top: 0;
		margin-bottom: 0;
		color: $color-mine-shaft;
		font-size: 18px;
	}
}

.dashboard-foundation,
.dashboard-arrangements,
.dashboard-rating {
	padding: 40px 50px;
}

.dashboard-rating {
	width: 180px;
	text-align: center;
	&__header {
		margin-bottom: 15px;
		color: $color-mine-shaft;
		font-size: 18px;
		font-weight: 700;
	}

	&__text {
		color: $color-curious-blue;
		font-size: 48px;
		font-weight: 700;
	}

}

.dashboard-arrangements {
	width: 260px;
	&__header {
		color: $color-franch-gray;
		font-size: 48px;
		font-weight: 700;
	}

	&__text {
		color: $color-gray;
		font-size: 18px;
	}

}

.dashboard-foundation {
	width: 260px;
	&__header {
		color: $color-curious-blue;
		font-size: 48px;
		font-weight: 700;
	}

	&__text {
		color: $color-gray;
		font-size: 18px;
	}
}

.dashboard-financials {
	position: relative;
	width: 760px;
	padding: 40px;
	&__header {
		margin-bottom: 40px;
		padding-right: 30px;
		padding-left: 30px;
		color: $color-mine-shaft;
		font-size: 24px;
		font-weight: 700;
	} 
	
	&__chart {
		margin-bottom: 40px;
		img {
			max-width: 100%;
		}
	}
	
	&__values {
		display: flex;
		justify-content: space-between;
		padding-right: 30px;
		padding-left: 30px;
	}
	
	&__item {
		width: 33.3%;
		text-align: center;
	}

	&__spend {
		margin-bottom: 5px;
		color: $color-mine-shaft;
		font-size: 14px;
		font-weight: 300;
	}

	&__value {
		margin-bottom: 10px;
		color: $color-mine-shaft;
		font-size: 14px;
		font-weight: 700;
	}

	&__spons {
		color: $color-curious-blue;
		font-size: 18px;
		font-weight: 700;
	}

	&__chart-point {
		position: absolute;
		top: 143px;
		left: 288px;
		width: 8px;
		height: 8px;
		border-radius: 50%;
		background: $color-curious-blue;
	}
	&__chart-description {
		position: absolute;
		top: 50px;
		left: 288px;
		width: 150px;
		height: 80px;
		padding: 15px 15px 15px  20px;
		box-shadow: 4px 4px 20px rgba(29, 149, 209, 0.1);
		border-radius: 5px;
		background-color: $color-white;
	}

	&__chart-value-1 {
		margin-right: 10px;
		color: $color-curious-blue;
		font-size: 24px;
	}
	
	&__chart-value-2 {
		color: $color-sunset-orange;
		font-size: 12px;
	}
	
	&__chart-value-3 {
		color: $color-curious-blue;
		font-size: 14px;
		font-weight: 700;
	}
}

.dashboard-sponsees {
	width: 760px;
	height: 200px;
	padding: 40px 70px;
	&__header {
		margin-bottom: 20px;
		color: $color-mine-shaft;
		font-size: 24px;
		font-weight: 700;
	}
	
	&__list {
		margin-top: 0;
		margin-bottom: 0;
		padding-left: 0;
		font-size: 0;
		list-style: none;
	}

	&__item {
		display: inline-block;
		width: 50%;
		margin-bottom: 15px;
		color: $color-gray;
		font-size: 14px;		
		&::before {
			display: inline-block;
			width: 14px;
			height: 10px;
			margin-right: 10px;
			content: '';
			background: url("../images/check.svg")
		}
	}
}